import React, {useState} from 'react'
import { Link } from 'react-router-dom'
import { Row, Col, Icon } from 'antd'

import './style.scss'

function Menu() {
  const [menuVisible, setMenuVisible] = useState(false)
  const toogleMenu = () => {
    setMenuVisible(!menuVisible)
  }

  return (
    <Row className="pd-header" type="flex" align="middle">
      <Col xs={22} lg={{ span: 8, offset: 2 }}>
        <Link to="/">
          <img
            className="pd-header-logo"
            src={`${process.env.PUBLIC_URL}/img/logo/wilco.png`}
            alt="Wilco Logo"
          />
        </Link>
      </Col>
      <Col xs={0} lg={12}>
        <div className="pd-header-menu">
          <Link to="/home" className="pd-header-menu-item">
            Home
          </Link>
          <Link to="/products" className="pd-header-menu-item">
            Products
          </Link>
          <Link to="/contact" className="pd-header-menu-item">
            Contact Us
          </Link>
          <Link to="/faq" className="pd-header-menu-item">
            FAQ
          </Link>
          <Link to="/about" className="pd-header-menu-item">
            About
          </Link>
        </div>
      </Col>
      <Col xs={2} lg={0} className="hamburger-menu" onClick={() => toogleMenu()}>
        <Icon type="menu"/>
      </Col>
      {
        menuVisible && (
          <div className="pd-header-menu--mobile" >
            <div className="header">
              <Col className="close-btn" onClick={() => toogleMenu()}>
                <Icon type="close" />
              </Col>
              <Col className="text-center" xs={24}>Menu</Col>
            </div>
            <div onClick={() => toogleMenu()}>
              <Link to="/home" className="item">
                Home
              </Link>
              <Link to="/products" className="item">
                Products
              </Link>
              <Link to="/contact" className="item">
                Contact Us
              </Link>
              <Link to="/faq" className="item">
                FAQ
              </Link>
              <Link to="/about" className="item">
                About
              </Link>
            </div>
          </div>
        )
      }
    </Row>
  )
}

export default Menu
